body{
    margin: 0px;
    padding: 0px;
    font-weight: 300;
    display: flex;
    overflow: hidden;
    // background-color: var(--blue);
}

ion-icon{
    color: var(--white);
}

::-webkit-scrollbar{
    width: 5px;
    // height: 5px;
}
::-webkit-scrollbar-track{
    background-color: transparent;
}

::-webkit-scrollbar-thumb{
    background-color: rgba(white, 0.3);
    height: 10px;
    // border-radius: 10px;
    opacity: 0.5;
}
